import { AuthResult } from '../../view/auth/auth-types';
import {
  AppError,
  DialogActions,
  ServerActions,
  UserActions,
} from '../constants/action-constants';
import { AnotherUsers, User } from './user-actions';

export type ServerMessage = {
  id: string;
  type: AppError | UserActions | DialogActions;
  payload: null | User | AnotherUsers | AuthResult;
};

export type ServerAction = {
  type: ServerActions;
  payload: ServerMessage;
};

export const incoming = (serverAction: ServerMessage) => ({
  type: ServerActions.INCOMING,
  payload: serverAction,
});

export const outcoming = (serverAction: ServerMessage) => ({
  type: ServerActions.OUTCOMING,
  payload: serverAction,
});
