import { ThunkMiddleware } from '@reduxjs/toolkit';
import { DialogActions, ServerActions, UserActions } from '../constants/action-constants';
import { sendMessage } from '../../core/services/send-message';
import { allInActiveUsers } from '../actions/user-actions';

export const getAllInactiveUsersMiddleware: ThunkMiddleware =
  ({ dispatch, getState }) =>
  (next) =>
  (action) => {
    if (
      action.type === ServerActions.INCOMING &&
      action.payload.type === UserActions.ALL_INACTIVE &&
      action.payload.payload &&
      'inactive' in action.payload.payload
    ) {
      dispatch(allInActiveUsers(action.payload.payload.inactive));

      if (Array.isArray(action.payload.payload.inactive)) {
        action.payload.payload.inactive.forEach((userLogin: string) => {
          const state = getState();
          if (state.users.userCurrent.login !== userLogin) {
            const payload = {
              login: userLogin,
              password: null,
            };
            sendMessage(DialogActions.MSG_FROM_USER, payload);
          }
        });
      }
    }

    return next(action);
  };
