import style from './style.module.css';
import React from 'react';

type ButtonProps = {
  text: string;
  callback: () => void;
  focus?: boolean;
  disabled?: boolean;
  small?: boolean;
};

const Button: React.FC<ButtonProps> = ({
  text,
  callback,
  focus = false,
  disabled = false,
  small = false,
}) => {
  return (
    <button
      type="button"
      className={small ? `${style.button} ${style.small}` : style.button}
      onClick={callback}
      disabled={disabled}
      autoFocus={focus}
    >
      {text}
    </button>
  );
};

export default Button;
