import { ThunkMiddleware } from '@reduxjs/toolkit';
import {
  DialogActions,
  ServerActions,
  UserActions,
} from '../constants/action-constants';
import { sendMessage } from '../../core/services/send-message';
import { allActiveUsers } from '../actions/user-actions';

export const getAllActiveUsersMiddleware: ThunkMiddleware =
  ({ dispatch, getState }) =>
  (next) =>
  (action) => {
    if (
      action.type === ServerActions.INCOMING &&
      action.payload.type === UserActions.ALL_ACTIVE &&
      action.payload.payload &&
      'active' in action.payload.payload
    ) {
      dispatch(allActiveUsers(action.payload.payload.active));

      if (Array.isArray(action.payload.payload.active)) {
        action.payload.payload.active.forEach((userLogin: string) => {
          const state = getState();
          if (state.users.userCurrent.login !== userLogin) {
            const payload = {
              login: userLogin,
              password: null,
            };
            sendMessage(DialogActions.MSG_FROM_USER, payload);
          }
        });
      }
    }

    return next(action);
  };
