import { Dialog, DialogAction } from '../actions/dialog-actions';
import { DialogActions } from '../constants/action-constants';

type DialogState = {
  selected: string | null,
  messages: Dialog[],
}

const initialState: DialogState = {
  selected: null,
  messages: new Array<Dialog>(),
}

const dialog = (state = initialState, action: DialogAction) => {
  switch(action.type) {
    case DialogActions.SELECT_USER: {
      return {
        ...state,
        selected: action.payload.login
      }
    }
    case DialogActions.MSG_CLEAR_HISTORY: {
      return initialState;
    }
    case DialogActions.MSG_FROM_USER: {
      const dialogs = state.messages.filter((messages) => messages.login !== action.payload.login);
      return {
        ...state,
        messages: [...dialogs, action.payload]
      }
    }
    case DialogActions.MSG_SEND: {
      const dialogsClone:Dialog[] = JSON.parse(JSON.stringify(state.messages));
      dialogsClone.forEach((dialog) => {
        if (dialog.login === action.payload.login) {
          dialog.messages.push(action.payload.messages[0]);
        }
      });
      return {
        ...state,
        messages: [...dialogsClone]
      }
    }
    case DialogActions.MSG_DELIVERED: {
      const dialogsClone:Dialog[] = JSON.parse(JSON.stringify(state.messages));
      dialogsClone.forEach((dialog) => {
        dialog.messages.forEach((message) => {
          if (message.id === action.payload.messages[0].id) {
            message.status.isDelivered = true;
          }
        });
      });
      return {
        ...state,
        messages: [...dialogsClone]
      }
    }
    case DialogActions.MSG_READED: {
      const dialogsClone:Dialog[] = JSON.parse(JSON.stringify(state.messages));
      dialogsClone.forEach((dialog) => {
        dialog.messages.forEach((message) => {
          if (message.id === action.payload.messages[0].id) {
            message.status.isReaded = true;
          }
        });
      });
      return {
        ...state,
        messages: [...dialogsClone]
      }
    }
    default: {
      return state;
    }
  }
};

export { dialog };
