import { FC, Fragment } from 'react';
import style from './style.module.css';
import Button from '../button/Button';

export type ModalInfoProps = {
  texts: string[];
  callback: () => void;
};

export const ModalInfo: FC<ModalInfoProps> = ({ texts, callback }) => {
  return (
    <Fragment>
      <div className={style.darken}></div>
      <div className={style.modal}>
        {texts.map((text, index) => (
          <label key={index} className={style.text}>
            {text}
          </label>
        ))}
        <Button text="Ok" callback={callback} focus={true} />
      </div>
    </Fragment>
  );
};
