import { ThunkMiddleware, configureStore } from '@reduxjs/toolkit';
import rootReducer from './reducers/redusers';
import { getMessagesMiddleware } from './middleware/get-messages-middleware';
import { deliveredMessageMiddleware } from './middleware/delivered-message-middleware';
import { readedMessageMiddleware } from './middleware/readed-message-middleware';
import { sendMessageMiddleware } from './middleware/send-message-middleware';
import { getUserMessagesMiddleware } from './middleware/get-user-messages-middleware';
import { getAllInactiveUsersMiddleware } from './middleware/get-all-inactive-users-middleware';
import { getAllActiveUsersMiddleware } from './middleware/get-all-active-users-middleware';
import { authCurrentUserMiddleware } from './middleware/auth-current-user-middleware';
import { authAnotherUserMiddleware } from './middleware/auth-another-user-middleware';

const middleware: ThunkMiddleware[] = [
  authCurrentUserMiddleware,
  authAnotherUserMiddleware,
  getMessagesMiddleware,
  deliveredMessageMiddleware,
  readedMessageMiddleware,
  sendMessageMiddleware,
  getUserMessagesMiddleware,
  getAllInactiveUsersMiddleware,
  getAllActiveUsersMiddleware,
];

export const store = configureStore({
  reducer: rootReducer,
  middleware: (getDefaultMiddleware) => getDefaultMiddleware().concat(...middleware)
});

export type RootState = ReturnType<typeof store.getState>;
export type AppDispatch = typeof store.dispatch;
