import { UserActions } from '../constants/action-constants';

export type User = {
  login: string | null;
  password: string | null;
};

export type AnotherUsers = {
  active: string[];
  inactive: string[];
};

export type UserAction = {
  type: UserActions;
  payload: User;
};

export type AnotherUserAction = {
  type: UserActions;
  payload: {
    active?: string[];
    inactive?: string[];
  };
};

export const login = (user: User): UserAction => ({
  type: UserActions.LOGIN,
  payload: {
    login: user.login,
    password: user.password,
  },
});

export const logout = (): UserAction => ({
  type: UserActions.LOGOUT,
  payload: {
    login: null,
    password: null,
  },
});

export const registration = (user: User): UserAction => ({
  type: UserActions.REGISTRATION,
  payload: {
    login: user.login,
    password: user.password,
  },
});

export const loginAnother = (user: User): UserAction => ({
  type: UserActions.LOGIN_ANOTHER,
  payload: {
    login: user.login,
    password: null,
  },
});

export const logoutAnother = (user: User): UserAction => ({
  type: UserActions.LOGOUT_ANOTHER,
  payload: {
    login: user.login,
    password: null,
  },
});

export const allActiveUsers = (usersActive: string[]): AnotherUserAction => ({
  type: UserActions.ALL_ACTIVE,
  payload: {
    active: usersActive,
  },
});

export const allInActiveUsers = (usersInActive: string[]): AnotherUserAction => ({
  type: UserActions.ALL_INACTIVE,
  payload: {
    inactive: usersInActive,
  },
});
