import { DialogActions } from "../constants/action-constants";

export type Dialog = {
  login: string | null;
  messages: Message[];
};

export type Message = {
  id: string | null,
  from: string | null,
  to: string | null,
  text: string,
  datetime: number,
  status: MessageStatus,
}

export type MessageStatus = {
  isDelivered: boolean,
  isReaded: boolean,
}

export type DialogAction = {
  type: DialogActions;
  payload: Dialog;
};

export const selectUser = (login: string): DialogAction => ({
  type: DialogActions.SELECT_USER,
  payload: {
    login: login,
    messages: [],
  },
});

export const clearMessagesHistory = (): DialogAction => ({
  type: DialogActions.MSG_CLEAR_HISTORY,
  payload: {
    login: '',
    messages: [],
  },
});

export const setMessagesHistory = (dialog: Dialog): DialogAction => ({
  type: DialogActions.MSG_FROM_USER,
  payload: {
    login: dialog.login,
    messages: dialog.messages,
  },
});

export const addMessageToHistory = (login: string, message: Message): DialogAction => ({
  type: DialogActions.MSG_SEND,
  payload: {
    login: login,
    messages: [message],
  },
});

// TODO     id
export const deliveredMessage = (idMessage: string): DialogAction => ({
  type: DialogActions.MSG_DELIVERED,
  payload: {
    login: null,
    messages: [{
      id: idMessage,
      from: null,
      to: null,
      text: '',
      datetime: 0,
      status: {
        isDelivered: true,
        isReaded: false,
      },
    }],
  },
});

// TODO     id
export const readedMessage = (idMessage: string): DialogAction => ({
  type: DialogActions.MSG_READED,
  payload: {
    login: null,
    messages: [{
      id: idMessage,
      from: null,
      to: null,
      text: '',
      datetime: 0,
      status: {
        isDelivered: true,
        isReaded: false,
      },
    }],
  },
});
