import {
  AnotherUserAction,
  AnotherUsers,
  User,
  UserAction,
} from '../../actions/user-actions';
import { UserActions } from '../../constants/action-constants';

const initialState: AnotherUsers = {
  active: new Array<string>(),
  inactive: new Array<string>(),
};

const usersAnother = (
  state = initialState,
  action: AnotherUserAction | UserAction
): AnotherUsers => {
  switch (action.type) {
    case UserActions.ALL_ACTIVE: {
      if ('active' in action.payload && Array.isArray(action.payload.active)) {
        return {
          active: new Array(...action.payload.active),
          inactive: state.inactive
        };
      }
      return state;
    }
    case UserActions.ALL_INACTIVE: {
      if ('inactive' in action.payload && Array.isArray(action.payload.inactive)) {
        return {
          active: state.active,
          inactive: new Array(...action.payload.inactive),
        };
      }
      return state;
    }
    case UserActions.LOGIN_ANOTHER: {
      if ('login' in action.payload) {
        const payload: User = action.payload;
        const inActiveUsers: string[] = state.inactive.filter(
          (login) => login !== payload.login
        );
        return {
          active: payload.login && state.active.indexOf(payload.login) === -1 ? [...state.active, payload.login] : [...state.active],
          inactive: [...inActiveUsers],
        };
      }
      return state;
    }
    case UserActions.LOGOUT_ANOTHER: {
      if ('login' in action.payload) {
        const payload: User = action.payload;
        const activeUsers: string[] = state.active.filter(
          (login) => login !== payload.login
        );
        return {
          active: [...activeUsers],
          inactive: payload.login && state.inactive.indexOf(payload.login) === -1 ? [...state.inactive, payload.login] : [...state.inactive],
        };
      }
      return state;
    }
    default:
      return state;
  }
};

export { usersAnother };
