import { ThunkMiddleware } from '@reduxjs/toolkit';
import { DialogActions, ServerActions } from '../constants/action-constants';
import { Message, setMessagesHistory } from '../actions/dialog-actions';
import { sendMessage } from '../../core/services/send-message';

export const getUserMessagesMiddleware: ThunkMiddleware =
  ({ dispatch, getState }) =>
  (next) =>
  (action) => {
    if (
      action.type === ServerActions.INCOMING &&
      action.payload.type === DialogActions.MSG_FROM_USER &&
      action.payload.payload &&
      'messages' in action.payload.payload
    ) {
      const state = getState();
      const currentUser = state.users.userCurrent.login;

      const messages: Message[] = action.payload.payload.messages;
      messages.forEach((message) => {
        if(message.to === currentUser && message.status.isDelivered === false) {
          const payload = {
            id: message.id,
          };
          sendMessage(DialogActions.MSG_DELIVERED, payload);
        }
      });
      dispatch(setMessagesHistory(action.payload.payload));
    }

    return next(action);
  };
